<template>
	<div>
		<div class="login_bg" v-show="!isLogin">
			<div class="login_head_box">
				<router-link :to="{'name':'login',params:{'key':key}}">
					<div class="login_head">
						<i class="icon iconfont icon-31wode"></i>
					</div>
					<p>已有账号登录</p>
				</router-link>		
			</div>
			<div class="register">
				<router-link :to="{'name':'register',params:{'key':this.key}}">
					<p>注册</p>
				</router-link>	
			</div>
		</div>
		
		<div class="login_success login_bg" v-show="isLogin">
			<div class="login_head_box">
				<router-link to="/login">
					<div class="login_head">
						<img :src="userInfo.img" alt="">
					</div>
					<p>{{userInfo.name}}</p>
				</router-link>		
			</div>
			<div class="register">
				<router-link to="/register">
					<p>{{userInfo.text || "暂无"}}</p>
				</router-link>	
			</div>
			
		</div>
		<mt-button type="danger" size="normal"  v-show="isLogin" @click="quit">退出</mt-button>
	</div>
</template>
<script>
import {mapState,mapGetters,mapActions} from "vuex";
	export default{
		data(){
			return {
				isShow:false,
				key:''
			}
		},
		computed:{
			...mapGetters({
				isLogin: 'isLogin',
				userInfo:'userInfo'
			})
		},
		mounted(){
			console.log(this.userInfo);
		},
		created(){
			this.axiosKey();
		},
		methods:{
			...mapActions([
				'setUser',
				'setInfo'
			]),
			axiosKey(){
				this.$axios.get("https://www.apiopen.top/createUserKey?appId=com.chat.peakchao&passwd=123456")
				.then(res=>{
					this.key = res.data.data.appkey;
				})
				.catch(err=>{
					console.log(err)
				})
			},
			quit(){
				this.setInfo([]);
				this.setUser(false);
				this.$Cookies.set("login",0);
			}
		},
	}

</script>
<style scoped lang="less">
	.mint-button{
			width:100%;
			margin-top:10px;	
		}
	.login_bg{
		height: 200px;
		width: 100%;
		background: url("https://ss0.bdstatic.com/70cFuHSh_Q1YnxGkpoWK1HF6hhy/it/u=3055598097,3360735557&fm=27&gp=0.jpg");
		position: relative;
		.register{
			a{
				p{
					color:#000;
				}
			}
		}
		.login_head_box{
			position: absolute;
			width: 100px;
			height: 80px;
			top: 60px;
			left: 50%;
			margin-left: -50px;
			z-index: 99;
			text-align: center;

			a{
				display: block;
				width: 100%;
				height: 100%;
				.login_head{
				background: #fff;
				width: 50px;
				height: 50px;
				border-radius: 50%;
				margin:  0 auto;
				line-height: 50px;
				text-align: center;
					i{
						font-size: 36px;
					}
				}
				p{
					color: #fff;
					position: relative;
					top: 5px;
				}	
			}
		
		}
		.register{
			width: 350px;
			height: 25px;
			line-height: 25px;
			text-align: center;
			position: absolute;
			bottom: 15px;
			left: 50%;
			margin-left: -175px;
			z-index: 99;
			a{
				font-size: 16px;
				color: #fff;	
				display: block;
				width: 100%; 
				height: 100%;
			}
		
		}
	}
	.login_success{
		.login_head_box{
			    top: 10px;
			a{	
				p{
					top: 10px;
				}
				.login_head{
					overflow:hidden;
					width: 98px;
		   			height: 98px;
					img{
						width:100%;
						height:100%;	
					}
				}
			}
		} 
		
		.register{
			bottom: 15px;
		}
	}
</style>


